<template>
	<div>
	<el-button id="butConnect" @click="clickConnect()">Record</el-button>
	<!-- <pre id="log"></pre> -->
</div>
</template>

<script>
	export default {
		data() {
			return {
        port: null,
        inputDone: null,
        reader: null,
      }
		},
		methods: {
			async connect() {
  				this.port = await navigator.serial.requestPort();
  				await this.port.open({ baudrate: 230400 });
  				let decoder = new TextDecoderStream();
  				// this.inputDone = this.port.readable.pipeTo(decoder.writable);
  				// let inputStream = decoder.readable;
  				// this.reader = inputStream.getReader();
          this.reader = this.port.readable.getReader();
  				this.readLoop(this.reader);
			},

      async disconnect() {
        if (this.reader) {
          await this.reader.cancel();
          // await this.inputDone.catch(() => {});
          this.reader = null;
          // this.inputDone = null;
        }
        await this.port.close();
        this.port = null;
      },

			async clickConnect() {
				if (this.port) {
  				await this.disconnect();
  				this.toggleUIConnected(false);
  				return;
				}
  				await this.connect();
  				this.toggleUIConnected(true);
			},

			async readLoop(r) {
  				// const log = document.getElementById("log");
  				while (true) {
    				const { value, done } = await r.read();
    				if (value.length == 10) {
                // console.log(value)
                const unit_uv = value.slice(1,5);
                const unit_lux = value.slice(6);
                const hex_uv = '0x' + this.buf2hex(unit_uv.buffer);
                const hex_lux = '0x' + this.buf2hex(unit_lux.buffer);
                // console.log(unit_lux);
                const u = new DataView(new ArrayBuffer(4))
                const l = new DataView(new ArrayBuffer(4))
                u.setUint32(0, hex_uv)
                l.setUint32(0, hex_lux)
                const uv = u.getFloat32(0)
                const lux = l.getFloat32(0)
                // const uvi = 0.0187*(0.00391*uv*uv + uv)
                // log.textContent = 'uv:' + uv + 'lux' +lux ;
                this.$emit('nuvData', uv.toFixed(1))
                this.$emit('nluxData', lux.toFixed(1))
    				}
    				if (done) {
      					console.log("[readLoop] DONE", done);
      					r.releaseLock();
      					break;
    				}
  				}
			},

			toggleUIConnected(connected) {
				const butConnect = document.getElementById("butConnect");
  				let lbl = "Record";
  				if (connected) {
    				lbl = "Stop";
  				}
  				butConnect.textContent = lbl;
			},

      buf2hex(buffer) {
        return Array.prototype.map.call(new Uint8Array(buffer), x => ('00' + x.toString(16)).slice(-2)).join('');
      }
		}
	}
</script>